import { builderFetch } from '../../utilities';

/**
 * Sets a new recipe title (post_title)
 *
 * @param {String} newTitle The recipe title
 * @return {Promise} The apiFetch promise
 */
const setRecipeTitle = ( newTitle ) => {
	// Event name
	const eventName = 'uap/builder/core/set-recipe-title';

	// Save the recipe title
	return builderFetch( {
		eventName: eventName,
		endpoint: 'change_post_title',
		method: 'POST',
		data: {
			// eslint-disable-next-line camelcase
			post_ID: UncannyAutomator._recipe.recipe_id,
			// eslint-disable-next-line camelcase
			post_title: newTitle
		},
		snackbar: {
			loading: _uo.utility.escHTML( wp.i18n.__( 'Updating recipe title', 'uncanny-automator' ) ),
			success: _uo.utility.escHTML( wp.i18n.__( 'Recipe title updated successfully', 'uncanny-automator' ) ),
		}
	} ).then( response => {
		// Check if the call was successful
		if ( response.success ) {
			// Send custom event to the document
			document.dispatchEvent( new CustomEvent( eventName, {
				detail: {
					recipeTitle: newTitle
				}
			} ) );
		}

		return response;
	} );
}

export default setRecipeTitle;
