// Import dependencies
import { unsafeCSS, css, html, LitElement } from 'lit';
import { customElement, property } from 'lit/decorators.js';
import { repeat } from 'lit/directives/repeat.js';
import { bracketsToStrong } from '@builder/components/utilities';
const { sortArray } = _uo.utility;

// Import style for web component
import styles from './web-component-style.scss';

// eslint-disable-next-line require-jsdoc
@customElement( 'uap-builder-item-loop-filter-directory' )
export class AutomatorBuilderItemLoopFilterDirectory extends LitElement {
	// Define style
	static styles = css`${ unsafeCSS( styles ) }`;

	// Properties
	// The unique ID of this loop block
	@property( { type: Number, attribute: 'loop-id' } ) accessor loopID;

	// Information about what has to be iterated (e.g. users, posts, tokens)
	@property( { type: Object, attribute: false } ) accessor iterableExpression;

	// eslint-disable-next-line require-jsdoc
	render() {
		return html`
			<div class="filter-directory">
				${ this._templateInstructions }
				${ this._templateFilterList }
			</div>
		`;
	}

	/**
	 * Renders instructions at the top of the dialog
	 *
	 * @return {TemplateResult|undefined} The instruction block
	 */
	get _templateInstructions() {
		// Add the instructions only if the loop is for users
		if ( this.iterableExpression.type !== 'users' ) {
			return;
		}

		return html`
			<uo-alert 
				type="info"
				heading=${ _uo.utility.escHTML( wp.i18n.__( "What's a loop filter?", 'uncanny-automator' ) ) }
			>
				${ _uo.utility.escHTML( wp.i18n.__( 'Loop filters decide which users will be included in the loop. Action filters within the block, on the other hand, choose what actions are performed for each individual user.', 'uncanny-automator' ) ) }
			</uo-alert>
		`;
	}

	/**
	 * Renders the list with the available loop filters
	 *
	 * @return {TemplateResult} The list with the loop filters
	 */
	get _templateFilterList() {
		return html`
			<uo-accordion
				list-search
				
				.searchPlaceholder="${ _uo.utility.escHTML( wp.i18n.__( 'Search loop filters', 'uncanny-automator' ) ) }"
				.noResultsText="${ _uo.utility.escHTML( wp.i18n.__( 'No results found', 'uncanny-automator' ) ) }"
			>
				${ this._templateFilterListItems }
			</uo-accordion>
		`;
	}

	/**
	 * Returns the loop filters grouped by integration
	 *
	 * @return {TemplateResult} The list of loop filters
	 */
	get _templateFilterListItems() {
		return repeat(
			// Get integrations with conditions
			sortArray.alphabetically(
				Object.entries( this.integrationsWithLoopFilters ),
				'[1].name'
			),
			// eslint-disable-next-line no-unused-vars
			( [ integrationCode, integration ] ) => integrationCode,
			// eslint-disable-next-line no-unused-vars
			( [ integrationCode, integration ] ) => html`
				<uo-accordion-item>
					<div slot="summary">
						<div class="filter-directory-integration">
							<uo-icon integration=${ integrationCode }></uo-icon> ${ integration.name }
						</div>
					</div>

					${ repeat(
						sortArray.alphabetically(
							Object.entries( integration.loop_filters ),
							'[1].sentence_readable'
						),
						// eslint-disable-next-line no-unused-vars
						( [ loopFilterCode, loopFilter ] ) => loopFilterCode,
						// eslint-disable-next-line no-unused-vars
						( [ loopFilterCode, loopFilter ] ) => {
							// Check if it's a loop filter of the type of the iterable expression
							if ( loopFilter.loop_type !== this.iterableExpression.type ) {
								return;
							}

							return html`
								<uo-accordion-item-li
									@click=${ event => this.selectLoopFilter( integrationCode, loopFilterCode ) }
								>
									${ bracketsToStrong( loopFilter.sentence ) }
								</uo-accordion-item-li>
							`
						}
					) }

				</uo-accordion-item>
			`
		);
	}

	/**
	 * Handles the selection of the loop filter
	 *
	 * @param {String} integrationCode The integration code of the loop filter
	 * @param {String} loopFilterCode The loop filter code
	 */
	selectLoopFilter( integrationCode, loopFilterCode ) {
		this.dispatchEvent(
			new CustomEvent(
				'uap-builder-loop-filter-directory-selected',
				{
					detail: {
						loopID: this.loopID,
						integrationCode: integrationCode,
						loopFilterCode: loopFilterCode
					}
				}
			)
		);
	}

	/**
	 * Returns an object with the integrations that have loop filters
	 *
	 * @return {Object} Integrations, key is the integration ID
	 */
	get integrationsWithLoopFilters() {
		return UncannyAutomator._core.directory.integrations.filterIntegrations( [
			UncannyAutomator._core.directory.integrations._filters.isAvailable,
			// Filter integrations that have loop filters of the type of the iterable expression
			integration => UncannyAutomator._core.directory.integrations._filters.hasLoopFilters(
				integration,
				this.iterableExpression.type
			)
		] );
	}
}