import { AutomatorBuilderPublishSetting } from '../_base';
import { html } from 'lit';
import { customElement } from 'lit/decorators.js';

// eslint-disable-next-line require-jsdoc
@customElement( 'uap-builder-publish-setting-throttle' )
export class AutomatorBuilderPublishSettingThrottle extends AutomatorBuilderPublishSetting {
	// Custom: Get the recipe type
	recipeType = UncannyAutomator._recipe.recipe_type;

	// Unique identifier for this setting.
	settingID = 'recipe_throttle';

	// Icon representing this setting in the UI.
	icon = 'gauge-max';

	// Define the helper text for the setting.
	helper = _uo.utility.escHTML( wp.i18n.__( 'Runs this recipe only once per specified period, even if triggered multiple times.', 'uncanny-automator' ) );

	/**
	 * Defines the fields available within this setting. Each object in this array represents a field that can be configured by the user, including input type, label, and other attributes.
	 */
	fieldsDirectory = [
		{
			option_code: 'ENABLED', // Unique code for the "Enable" toggle.
			input_type: 'checkbox', // Specifies the input type as integer.
			is_toggle: true, // Specifies that this field is a toggle.
			label: _uo.utility.escHTML( wp.i18n.__( 'Enable throttling', 'uncanny-automator' ) ), // Label for the "Enable" toggle.
			description: _uo.utility.escHTML( wp.i18n.__( 'When enabled, this recipe will only run once within the defined time period, even if triggered multiple times. For example, if set to "1 day," the recipe will run only once per day, regardless of how many times it is triggered.', 'uncanny-automator' ) ), // Field description shown to the user.
		},

		// Add "Throttle scope" only to "Logged-in" recipes
		...( this.recipeType === 'user' ? [ {
			option_code: 'PER_RECIPE_OR_USER',
			input_type: 'select',
			label: _uo.utility.escHTML( wp.i18n.__( 'Throttle scope', 'uncanny-automator' ) ),
			description: _uo.utility.escHTML( wp.i18n.__( 'Define whether the limit applies globally or per user.', 'uncanny-automator' ) ),
			options_show_id: false,
			options: [
				{
					value: 'recipe',
					text: _uo.utility.escHTML( wp.i18n.__( 'Per recipe  Shared limit for all users', 'uncanny-automator' ) ),
				},
				{
					value: 'user',
					text: _uo.utility.escHTML( wp.i18n.__( 'Per user  Each user has their own independent limit', 'uncanny-automator' ) ),
				}
			],
			required: true,
			dynamic_visibility: {
				default_state: 'hidden',
				visibility_rules: [
					{
						operator: 'AND',
						rule_conditions: [
							{
								option_code: 'ENABLED',
								compare: '==',
								value: true,
							},
						],
						resulting_visibility: 'show',
					}
				]
			}
		} ] : [] ),

		{
			option_code: 'NUMBER', // Unique code for the "Number" field.
			input_type: 'int', // Specifies the input type as integer.
			label: _uo.utility.escHTML( wp.i18n.__( 'Throttle duration', 'uncanny-automator' ) ),
			description: _uo.utility.escHTML( wp.i18n.__( 'Set how long the throttle lasts (used with the time unit below).', 'uncanny-automator' ) ),
			placeholder: _uo.utility.escHTML( wp.i18n.__( 'e.g. 30', 'uncanny-automator' ) ),
			min_number: 1, // Minimum value allowed for "Times" (must be 1 or greater).
			supports_tokens: false, // Specifies that this field does not support tokens.
			required: true,
			dynamic_visibility: {
				default_state: 'hidden',
				visibility_rules: [
					{
						operator: 'AND',
						rule_conditions: [
							{
								option_code: 'ENABLED',
								compare: '==',
								value: true,
							},
						],
						resulting_visibility: 'show',
					}
				]
			}
		},
		{
			option_code: 'UNIT', // Unique code for the "Unit" field.
			input_type: 'select', // Specifies the input type as integer.
			label: _uo.utility.escHTML( wp.i18n.__( 'Duration unit', 'uncanny-automator' ) ),
			description: _uo.utility.escHTML( wp.i18n.__( 'Select the unit of time (seconds, minutes, hours, days, or years).', 'uncanny-automator' ) ),
			supports_tokens: false, // Specifies that this field does not support tokens.
			options_show_id: false, // Specifies that the option ID should not be shown in the label.
			required: true,
			options: [
				{
					value: 'seconds',
					text: _uo.utility.escHTML( wp.i18n.__( 'Seconds', 'uncanny-automator' ) ),
				},
				{
					value: 'minutes',
					text: _uo.utility.escHTML( wp.i18n.__( 'Minutes', 'uncanny-automator' ) ),
				},
				{
					value: 'hours',
					text: _uo.utility.escHTML( wp.i18n.__( 'Hours', 'uncanny-automator' ) ),
				},
				{
					value: 'days',
					text: _uo.utility.escHTML( wp.i18n.__( 'Days', 'uncanny-automator' ) ),
				},
				{
					value: 'weeks',
					text: _uo.utility.escHTML( wp.i18n.__( 'Weeks', 'uncanny-automator' ) ),
				},
				{
					value: 'months',
					text: _uo.utility.escHTML( wp.i18n.__( 'Months', 'uncanny-automator' ) ),
				},
				{
					value: 'years',
					text: _uo.utility.escHTML( wp.i18n.__( 'Years', 'uncanny-automator' ) ),
				},
			],
			dynamic_visibility: {
				default_state: 'hidden',
				visibility_rules: [
					{
						operator: 'AND',
						rule_conditions: [
							{
								option_code: 'ENABLED',
								compare: '==',
								value: true,
							},
						],
						resulting_visibility: 'show',
					}
				]
			}
		}
	];

	/**
	 * Renders the label template.
	 * @return {TemplateResult} The label template.
	 */
	get _templateLabel() {
		return html`
			<div class="setting-label">
				${ _uo.utility.escHTML( wp.i18n.__( 'Throttle', 'uncanny-automator' ) ) }: <uo-chip color="info" size="xsmall" filled>${ _uo.utility.escHTML( wp.i18n.__( 'New', 'uncanny-automator' ) ) }</uo-chip>
			</div>
		`;
	}

	/**
	 * Retrieves a readable version of the "Throttle" field value for display.
	 *
	 * @return {Number} The number of throttle
	 */
	get readableValue() {
		const isEnabled     = this.getFieldValue( 'ENABLED' );
		const number        = this.getFieldValue( 'NUMBER' );
		const unit          = this.getFieldValue( 'UNIT' );
		const throttleScope = this.recipeType === 'user' ? this.getFieldValue( 'PER_RECIPE_OR_USER' ) : 'recipe';

		// Check if the setting is not enabled
		if ( _uo.utility.isEmpty( isEnabled ) || ! isEnabled ) {
			return _uo.utility.escHTML( wp.i18n.__( 'Unlimited', 'uncanny-automator' ) );
		}

		// If the throttle scope is per recipe, return the formatted duration
		if ( throttleScope === 'recipe' ) {
			return _uo.utility.getFormattedDuration( unit, number, true );
		}

		// If the throttle scope is per user, return the formatted duration and per user text
		return _uo.utility.sprintf(
			_uo.utility.escHTML( wp.i18n.__( '%1$s, per user', 'uncanny-automator' ) ),
			_uo.utility.getFormattedDuration( unit, number, true )
		);
	}

	/**
	 * Opens the helper for the setting.
	 */
	helperCallback() {
		_uo.utility.openWindow( {
			url: 'https://automatorplugin.com/knowledge-base/?utm_medium=publish_metabox&utm_content=recipe_throttle_question_icon',
			w: 450
		} )
	}
}
