import $ from 'jquery';

import {
	itemsObject,
	getDOMElements,
} from '../common/utilities';

import ItemCreatorIntegrations from '../item/integrations-grid.js';
import Actions                 from '../item/actions';

class MetaboxFooter {

	constructor( itemType ){
		this.itemType = itemType;

		if ( this.shouldCreateMetaboxFooter() ){
			this.createFooter();
		}
	}

	shouldCreateMetaboxFooter(){
		// Check if we should create the metabox
		// For the triggers we're going to create it always,
		// but for the actions we're going to create only if there is at least one action

		let create = false;

		if ( this.itemType == 'trigger' ){
			// Get triggers
			let recipeActions = itemsObject( 'get', {
				filter: {
					itemType: 'trigger'
				}
			});

			// Check if has triggers
			let hasTriggers = ! _uo.utility.isEmpty( recipeActions );

			// Create metabox if hasTriggers
			if ( ( hasTriggers || ( ! UncannyAutomatorBackend._site.automator.is_pro_active ) ) || UncannyAutomator._recipe.recipe_type == 'anonymous' ){
				create = true;
			}
		}

		if ( this.itemType == 'action' ){
			// Get actions
			let recipeActions = itemsObject( 'get', {
				filter: {
					itemType: 'action'
				}
			});

			// Check if has actions
			let hasActions = this.hasLoops || ! _uo.utility.isEmpty( recipeActions );

			// Create metabox if hasActions
			if ( hasActions || ! _uo.utility.isEmpty( UncannyAutomator.recipe.conditions ) ){
				create = true;
			}
		}

		return create;
	}

	createFooter(){
		// Create error container
		let container  = $( '<div/>', { 'class': 'metabox__footer' });

		// "Add another" button
		const $addAnother = document.createElement( 'uo-button' );
		$addAnother.setAttribute( 'color', 'secondary' );

		// Add button, but don't add it in the triggers metabox for anonymous recipes
		if ( this.itemType == 'action' || ( this.itemType == 'trigger' && UncannyAutomator._recipe.recipe_type == 'user' ) ){
			// Append "Add another" button
			container.append( $addAnother );
		}

		if ( this.itemType == 'trigger' ){
			// Do only for "user" recipes
			if ( UncannyAutomator._recipe.recipe_type == 'user' ){
				// Set text to the "Add Another" button
				$addAnother.innerText = _uo.utility.escHTML( wp.i18n.__( 'Add trigger', 'uncanny-automator' ) );
			}

			// Do only for "anonymous" recipes
			if ( UncannyAutomator._recipe.recipe_type == 'anonymous' ){
				// Create and append anonymous notice
				let $anonymousNotice = this.createAnonymousNotice();
				container.append( $anonymousNotice );
			}
		}

		if ( this.itemType == 'action' ){
			// Set text to the "Add Another" button
			$addAnother.innerText = _uo.utility.escHTML( wp.i18n.__( 'Add action', 'uncanny-automator' ) );
			container.addClass('add-action-btn');
		}

		// Add button to add filter
		if ( this.itemType == 'action' ){
			container.get(0).insertAdjacentHTML( 'beforeEnd', `
				<uo-button-dropdown class="uap-metabox-footer-buttons">
					<uo-button 
						slot="target" 
						color="secondary"
					>
						${ _uo.utility.escHTML( wp.i18n.__( 'Add', 'uncanny-automator' ) ) } <uo-icon id="angle-down"></uo-icon>
					</uo-button>

					<ua-builder-conditions-add-button
						color="transparent"
					>
						${ _uo.utility.escHTML( wp.i18n.__( 'Filter', 'uncanny-automator' ) ) }
					</ua-builder-conditions-add-button>

					<uap-builder-loop-add-user-loop
						color="transparent"
					></uap-builder-loop-add-user-loop>

					<uap-builder-loop-add-post-loop
						color="transparent"
					></uap-builder-loop-add-post-loop>

					<uap-builder-loop-add-token-loop
							parent-id=${ UncannyAutomator._recipe.recipe_id }
						color="transparent"
					></uap-builder-loop-add-token-loop>
				</uo-button-dropdown>
			` );
		}

		// Bind button
		if ( ( this.itemType == 'trigger' && UncannyAutomator._recipe.recipe_type == 'user' ) || this.itemType == 'action' ){
			// Listen clicks to the "Add another" button
			$addAnother.addEventListener( 'click', () => {
				// Close any open conditions actions
				new Actions();

				// Multiple triggers are only allowed in the pro version
				// So we're only doing this for actions
				new ItemCreatorIntegrations( this.itemType );
			} );
		}

		// Append to the metabox
		let metabox = getDOMElements( 'metabox', {
			type: this.itemType
		})

		metabox.append( container );
	}

	createAnonymousNotice(){
		let $notice = $( `
			<div class="description">
				${ _uo.utility.escHTML( wp.i18n.__( 'This recipe type supports one trigger per recipe.', 'uncanny-automator' ) ) } <a href="${ _uo.utility.addParametersUTM( 'https://automatorplugin.com/knowledge-base/managing-triggers/', {
						'medium':  'anonymous_recipe_one_trigger',
						'content': 'learn_more'
					}) }" target="_blank">${ _uo.utility.escHTML( wp.i18n.__( 'Learn more', 'uncanny-automator' ) ) } <uo-icon id="external-link"></uo-icon></a>
			</div>
		` );

		return $notice;
	}

	get hasLoops () {
		return UncannyAutomator._recipe.actions.items.filter( item => item.type === 'loop' ).length > 0;	
	}
}

export default MetaboxFooter;