// Import dependencies
import { unsafeCSS, css, html, LitElement } from 'lit';
import { customElement } from 'lit/decorators.js';

// Import style for web component
import styles from './web-component-style.scss';

// eslint-disable-next-line require-jsdoc
@customElement( 'uap-builder-publish-status-toggle' )
export class AutomatorBuilderPublishStatusToggle extends LitElement {
	// Define style
	static styles = css`${ unsafeCSS( styles ) }`;

	// eslint-disable-next-line require-jsdoc
	render() {
		// Only render the button is the recipe DOES NOT have the "Run now" button
		if ( UncannyAutomator._recipe.miscellaneous.has_run_now ) {
			return;
		}

		return html`
			<uo-field-input-switch
				size="large"

				?checked=${ UncannyAutomator._recipe.is_recipe_on }

				label-on=${ _uo.utility.escHTML( wp.i18n.__( 'Live', 'uncanny-automator' ) ) }
				label-off=${ _uo.utility.escHTML( wp.i18n.__( 'Draft', 'uncanny-automator' ) ) }
				label-placement="left"
				
				.processChangeRequest=${ async isChecked => {
					return UncannyAutomator._core.recipe.setRecipeStatus( isChecked );
				} }
			></uo-field-input-switch>
		`;
	}
}